// src/stores/hotkeyStore.js
import { writable } from "svelte/store";

function createHotkeyStore() {
    const { subscribe, update } = writable({});

    function register(keyCombo, callback) {
        update((map) => {
            map[keyCombo] = callback;
            return map;
        });
    }

    function unregister(keyCombo) {
        update((map) => {
            delete map[keyCombo];
            return map;
        });
    }

    return { subscribe, register, unregister };
}

export const hotkeys = createHotkeyStore();
