<script>
  import { onMount, onDestroy, createEventDispatcher } from 'svelte';
  import { hotkeys } from '@src/stores/hotkeyStore.js';

  export let combo; // v d "Ctrl+S"
  const dispatch = createEventDispatcher();

  function handler(e) {
    dispatch('pressed', { detail: e });
  }

  onMount(() => {
    hotkeys.register(combo, handler);
  });

  onDestroy(() => {
    hotkeys.unregister(combo);
  });
</script>
