// api.js
export default class ApiClient {
    constructor(baseURL = '') {
        this.baseURL = import.meta.env.VITE_API_BASE_URL || baseURL || `${location.protocol}//${location.host}`;
    }

    async request(endpoint, method = 'GET', data = null, headers = {}, responseType = 'json') {
        const url = `${this.baseURL}${endpoint}`;
        const options = {
            method,
            headers: {
                'Content-Type': data instanceof FormData ? undefined : 'application/json',
                ...headers,
            },
        };

        if (data) {
            options.body = data instanceof FormData ? data : JSON.stringify(data);
        }

        try {
            const response = await fetch(url, options);
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }

            switch (responseType) {
                case 'json':
                    return await response.json();
                case 'text':
                    return await response.text();
                case 'blob':
                    return await response.blob();
                case 'arrayBuffer':
                    return await response.arrayBuffer();
                case 'formData':
                    return await response.formData();
                default:
                    return response; // raw Response object
            }
        } catch (error) {
            console.error('API Request failed:', error);
            throw error;
        }
    }

    async get(endpoint, headers = {}, responseType = 'json') {
        return this.request(endpoint, 'GET', null, headers, responseType);
    }

    async post(endpoint, data, headers = {}, responseType = 'json') {
        return this.request(endpoint, 'POST', data, headers, responseType);
    }

    async put(endpoint, data, headers = {}, responseType = 'json') {
        return this.request(endpoint, 'PUT', data, headers, responseType);
    }

    async delete(endpoint, headers = {}, responseType = 'json') {
        return this.request(endpoint, 'DELETE', null, headers, responseType);
    }
}

