<script>
  import { Maximize, Minimize, RefreshCw, X, Expand, Shrink } from 'lucide-svelte';
  // import Icon from '@components/Icon.svelte';
  import { onMount, onDestroy } from 'svelte';

  export let open = false;
  export let title = 'Untitle';
  export let onClose = () => {
    open = false;
  };
  export let onConfirm = () => {};
  export let width = '80vw';
  export let height = 'auto';
  export let closeOnOutsideClick = true;

  let modalRef;
  let offsetX = 0;
  let offsetY = 0;
  let isDragging = false;
  let startX, startY;
  let isFullscreen = false; // trng thi modal c ang phng to
  let isMoved = false; //  trng thi modal c b ko lch hay khng

  function onMouseDown(event) {
    if (!modalRef || isFullscreen) return;

    isDragging = true;
    startX = event.clientX;
    startY = event.clientY;

    const style = window.getComputedStyle(modalRef);
    const matrix = new DOMMatrixReadOnly(style.transform);
    offsetX = matrix.m41;
    offsetY = matrix.m42;

    window.addEventListener('mousemove', onMouseMove);
    window.addEventListener('mouseup', onMouseUp);
  }

  function onMouseMove(event) {
    if (!isDragging) return;

    const dx = event.clientX - startX;
    const dy = event.clientY - startY;

    modalRef.style.transform = `translate(${offsetX + dx}px, ${offsetY + dy}px)`;
  }

  function onMouseUp() {
    isDragging = false;
    window.removeEventListener('mousemove', onMouseMove);
    window.removeEventListener('mouseup', onMouseUp);

    //  Kim tra xem modal c b lch khng
    if (modalRef) {
      const style = window.getComputedStyle(modalRef);
      const matrix = new DOMMatrixReadOnly(style.transform);
      isMoved = !(matrix.m41 === 0 && matrix.m42 === 0);
    }
  }

  function resetPosition() {
    if (modalRef) {
      modalRef.style.transform = 'translate(0px, 0px)';
      isMoved = false; // reset li
    }
  }

  function toggleFullscreen() {
    isFullscreen = !isFullscreen;
    resetPosition();
  }

  function toggleFull() {
    if (!modalRef) return;

    if (!isFullscreen) {
      modalRef.requestFullscreen?.();
    } else {
      document.exitFullscreen?.();
    }
    isFullscreen = !isFullscreen;
  }

  function onFullscreenChange() {
    isFullscreen = !!document.fullscreenElement;
  }

  onMount(() => {
    document.addEventListener('fullscreenchange', onFullscreenChange);
  });

  onDestroy(() => {
    document.removeEventListener('fullscreenchange', onFullscreenChange);
  });

  function onBackdropClick() {
    if (closeOnOutsideClick) {
      onClose(); // gi hm onClose
    }
  }
</script>

{#if open}
  <!-- Backdrop -->
  <div class="fixed inset-0 z-40 bg-white/10 backdrop-blur-sm transition-opacity" on:click={onBackdropClick}></div>

  <!-- Modal container -->
  <div class="fixed inset-0 z-50 flex items-center justify-center pointer-events-none">
    <div
      bind:this={modalRef}
      class="bg-white rounded-2xl shadow-2xl border border-gray-200 overflow-hidden relative pointer-events-auto transition-transform transition-all duration-300"
      style="width: {isFullscreen ? '100vw' : width}; height: {isFullscreen
        ? '100vh'
        : height}; max-height: 90vh; transform: translate(0px, 0px);"
      on:click|stopPropagation>
      <!-- Header -->
      <div
        class="flex items-center justify-between px-6 py-4 border-b bg-gradient-to-r from-gray-50 to-white cursor-move select-none"
        on:mousedown={onMouseDown}>
        <h2 class="text-xl font-semibold text-gray-800">{title}</h2>

        <div class="flex gap-3 items-center">
          <!--  Ch hin Reset Position khi modal b lch -->
          {#if isMoved}
            <button
              class="p-2 rounded hover:bg-gray-100 transition"
              on:click={resetPosition}
              title="t li v tr"
              aria-label="Reset position">
              <RefreshCw class="w-5 h-5 text-gray-600 hover:text-blue-600" />
            </button>
          {/if}

          <!-- Toggle Fullscreen (phng to modal ton mn hnh trnh duyt) -->
          <button
            class="p-2 rounded hover:bg-gray-100 transition"
            on:click={toggleFullscreen}
            title="Chuyn ch  fullscreen"
            aria-label="Toggle fullscreen">
            {#if isFullscreen}
              <Shrink class="w-5 h-5 text-gray-600 hover:text-blue-600" />
            {:else}
              <Expand class="w-5 h-5 text-gray-600 hover:text-blue-600" />
            {/if}
          </button>

          <!-- Maximize / Minimize (phng to modal trong app, khng fullscreen) -->
          <button
            class="p-2 rounded hover:bg-gray-100 transition"
            on:click={toggleFull}
            title={isFullscreen ? 'Thu nh ca s' : 'Phng to ca s'}
            aria-label="Maximize or minimize modal">
            {#if isFullscreen}
              <Minimize class="w-5 h-5 text-gray-600 hover:text-blue-600" />
            {:else}
              <Maximize class="w-5 h-5 text-gray-600 hover:text-blue-600" />
            {/if}
          </button>

          <!-- Close -->
          <button
            class="p-2 rounded hover:bg-gray-100 transition"
            on:click={onClose}
            title="ng"
            aria-label="Close modal">
            <X class="w-6 h-6 text-gray-600 hover:text-red-600" />
          </button>
        </div>
      </div>

      <!-- Body -->
      <div class="px-6 py-4 overflow-y-auto h-[calc(100%-130px)]">
        <slot />
      </div>

      <!-- Footer -->
      <div class="flex justify-end gap-2 px-6 py-2 border-t bg-gray-50">
        <button
          class="px-4 py-2 text-sm font-medium text-gray-600 bg-white border border-gray-300 rounded-lg hover:bg-gray-100"
          on:click={onClose}>
          Cancel
        </button>
        <button
          class="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700"
          on:click={onConfirm}>
          OK
        </button>
      </div>
    </div>
  </div>
{/if}
