<script>
  let activeTab = 'account';
  let searchQuery = '';

  const tabs = [
    { id: 'account', label: 'Account', icon: '' },
    { id: 'security', label: 'Security', icon: '' },
    { id: 'notifications', label: 'Notifications', icon: '' },
    { id: 'system', label: 'System', icon: '' }
  ];

  function setTab(tab) {
    activeTab = tab;
  }

  function save() {
    alert('Changes saved!');
  }
</script>

<div class="min-h-screen flex bg-gray-100">
  <!-- Sidebar -->
  <aside class="w-64 bg-white shadow-lg flex flex-col">
    <div class="p-6 font-bold text-xl text-blue-600 border-b"> Settings</div>
    <nav class="flex-1 p-4 space-y-2">
      {#each tabs as tab}
        <button
          class="w-full flex items-center gap-3 px-4 py-2 rounded-lg transition
                 {activeTab === tab.id ? 'bg-blue-100 text-blue-700 font-medium' : 'hover:bg-gray-100 text-gray-700'}"
          on:click={() => setTab(tab.id)}>
          <span>{tab.icon}</span>
          {tab.label}
        </button>
      {/each}
    </nav>
  </aside>

  <!-- Content -->
  <main class="flex-1 p-8 space-y-6">
    <!-- Search -->
    <div class="flex justify-between items-center">
      <h1 class="text-2xl font-semibold text-gray-800 capitalize">
        {activeTab} settings
      </h1>
      <input
        type="text"
        bind:value={searchQuery}
        placeholder="Search settings..."
        class="w-64 px-4 py-2 rounded-lg border border-gray-300 shadow-sm
               focus:border-blue-500 focus:ring focus:ring-blue-200" />
    </div>

    <!-- Dynamic Content -->
    {#if activeTab === 'account'}
      <section class="bg-white p-6 rounded-xl shadow space-y-4">
        <h2 class="text-lg font-medium text-gray-800">Account Info</h2>
        <div>
          <label class="block text-sm text-gray-600">Full Name</label>
          <input
            type="text"
            placeholder="Your name"
            class="mt-1 w-full rounded-lg border-gray-300 shadow-sm focus:ring focus:ring-blue-200" />
        </div>
        <div>
          <label class="block text-sm text-gray-600">Email</label>
          <input
            type="email"
            placeholder="your@email.com"
            class="mt-1 w-full rounded-lg border-gray-300 shadow-sm focus:ring focus:ring-blue-200" />
        </div>
        <button class="mt-4 px-4 py-2 rounded-lg bg-blue-600 text-white shadow hover:bg-blue-700" on:click={save}>
          Save Account
        </button>
      </section>
    {:else if activeTab === 'security'}
      <section class="bg-white p-6 rounded-xl shadow space-y-4">
        <h2 class="text-lg font-medium text-gray-800">Security</h2>
        <div>
          <label class="block text-sm text-gray-600">Change Password</label>
          <input
            type="password"
            placeholder="New password"
            class="mt-1 w-full rounded-lg border-gray-300 shadow-sm focus:ring focus:ring-blue-200" />
        </div>
        <button class="mt-4 px-4 py-2 rounded-lg bg-blue-600 text-white shadow hover:bg-blue-700">
          Update Password
        </button>
      </section>
    {:else if activeTab === 'notifications'}
      <section class="bg-white p-6 rounded-xl shadow space-y-4">
        <h2 class="text-lg font-medium text-gray-800">Notifications</h2>
        <label class="flex items-center gap-3">
          <input type="checkbox" class="rounded text-blue-600" />
          Email alerts
        </label>
        <label class="flex items-center gap-3">
          <input type="checkbox" class="rounded text-blue-600" />
          Push notifications
        </label>
      </section>
    {:else if activeTab === 'system'}
      <section class="bg-white p-6 rounded-xl shadow space-y-4">
        <h2 class="text-lg font-medium text-gray-800">System Settings</h2>
        <label class="block text-sm text-gray-600">Language</label>
        <select class="mt-1 w-full rounded-lg border-gray-300 shadow-sm focus:ring focus:ring-blue-200">
          <option>English</option>
          <option>Vietnamese</option>
          <option>Japanese</option>
        </select>
      </section>
    {/if}
  </main>
</div>
