const shutterSound = new Audio("/sounds/shutter.mp3");
const recordingSound = new Audio("/sounds/recording.mp3");
const stopRecordingSound = new Audio("/sounds/stop-recording.mp3");

function playSound(audio) {
    audio.currentTime = 0; // tua v u  pht li
    audio.play().catch(err => console.warn("Khng pht c m thanh:", err));
}

export function playShutter() {
    playSound(shutterSound);
}

export function playRecording() {
    playSound(recordingSound);
}

export function playStopRecording() {
    playSound(stopRecordingSound);
}

