<script>
  import { onMount, onDestroy } from 'svelte';
  import { postImage, streamInput } from '$lib/api.js';
  import { connectWebSocket, sendChunk, sendStop, closeSocket } from '$lib/socket.js';
  import { playShutter, playRecording, playStopRecording } from '$lib/audio.js';
  import { ArrowLeft } from 'lucide-svelte';

  let facingMode = 'environment'; // mc nh camera sau
  let showMore = false;
  let videoElement;
  let mediaStream;
  let mediaRecorder;
  let chunks = [];
  let recordedChunks = [];

  let recording = false;

  export let onClose;

  onMount(async () => {
    await initCamera();
  });

  onDestroy(() => {
    stopCamera();
    console.log('Camera component destroyed, camera stopped.', new Date().toLocaleTimeString());
  });

  export async function initCamera1() {
    const isIOS = /iPhone|iPad|iPod/i.test(navigator.userAgent);

    try {
      if (isIOS) {
        // Nu l iPhone/iPad, u tin camera sau
        mediaStream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: { exact: 'environment' } },
          audio: false
        });
      } else {
        // Nu l my tnh / thit b khc, dng camera mc nh
        mediaStream = await navigator.mediaDevices.getUserMedia({
          video: true,
          audio: false
        });
      }
    } catch (err) {
      console.warn(' Khng ly c camera sau hoc li khc, fallback v camera mc nh', err);
      // Fallback v camera thng
      mediaStream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: false
      });
    }

    videoElement.srcObject = mediaStream;
  }

  async function initCamera() {
    try {
      playRecording();
      // stop stream c nu c
      if (mediaStream) {
        mediaStream.getTracks().forEach((track) => track.stop());
      }

      mediaStream = await navigator.mediaDevices.getUserMedia({
        video: { facingMode },
        audio: false
      });

      if (videoElement) {
        videoElement.srcObject = mediaStream;
      }
    } catch (err) {
      console.warn(' Khng m c camera', err);
    }
  }

  function toggleCamera() {
    facingMode = facingMode === 'user' ? 'environment' : 'user';
    initCamera();
    closeMore();
  }

  // --- STOP CAMERA ---
  // Tt tt c track + clear reference  camera thc s tt
  export function stopCamera() {
    if (mediaStream) {
      mediaStream.getTracks().forEach((track) => track.stop());
      mediaStream = null; // clear bin stream
    }
    if (videoElement) {
      videoElement.srcObject = null; // clear khi video tag
    }
  }

  export async function capture() {
    playShutter(); // Pht m thanh chp nh
    const canvas = document.createElement('canvas');
    canvas.width = videoElement.videoWidth;
    canvas.height = videoElement.videoHeight;
    const ctx = canvas.getContext('2d');
    ctx.drawImage(videoElement, 0, 0);

    const blob = await new Promise((resolve) => canvas.toBlob(resolve, 'image/jpeg'));
    /*
        console.log("Captured image blob:", blob);
        const formData = new FormData();
        formData.append("image", blob, "capture.jpg");

        await fetch("/capture", {
            method: "POST",
            body: formData,
        });
        */
    postImage(blob);
  }

  // --- START RECORDING ---
  // Ghi hnh v stream chunk theo tng batch 2MB
  export async function startRecording() {
    chunks = []; // reset
    mediaRecorder = new MediaRecorder(mediaStream, {
      mimeType: 'video/webm'
    });

    let buffer = [];
    let totalSize = 0;
    const MAX_MB = 2;

    mediaRecorder.ondataavailable = async (e) => {
      if (e.data.size > 0) {
        chunks.push(e.data); //  lu  khi stop c d liu
        buffer.push(e.data);
        totalSize += e.data.size;

        if (totalSize / (1024 * 1024) >= MAX_MB) {
          const batch = new Blob(buffer, { type: 'video/webm' });
          console.log('Chunk size:', batch, new Date().toLocaleTimeString());
          await streamInput(batch, false);
          buffer = [];
          totalSize = 0;
        }
      }
    };

    mediaRecorder.onstop = async () => {
      // gom ton b d liu  c trong chunks
      const finalBlob = new Blob(chunks, { type: 'video/webm' });
      await streamInput(finalBlob, true);
    };

    mediaRecorder.start(2000); // mi 2s bn ra mt dataavailable
    recording = true;
  }

  function getSupportedMimeType() {
    const possibleTypes = [
      'video/webm;codecs=vp9,opus',
      'video/webm;codecs=vp8,opus',
      'video/webm',
      'video/mp4;codecs=h264,aac',
      'video/mp4'
    ];

    for (const type of possibleTypes) {
      if (MediaRecorder.isTypeSupported(type)) {
        return type;
      }
    }
    return ''; // fallback  MediaRecorder t chn
  }

  function startRecording2() {
    recordedChunks = [];

    const mimeType = getSupportedMimeType();
    console.log(' Recording with mimeType:', mimeType || 'default');

    mediaRecorder = new MediaRecorder(mediaStream, mimeType ? { mimeType } : {});

    mediaRecorder.ondataavailable = (e) => {
      if (e.data.size > 0) {
        recordedChunks.push(e.data);
      }
    };

    mediaRecorder.onstop = () => {
      const blob = new Blob(recordedChunks, { type: mimeType || 'video/webm' });
      const url = URL.createObjectURL(blob);

      // Ti file v
      const a = document.createElement('a');
      a.href = url;
      a.download = `recorded_video.${blob.type.includes('mp4') ? 'mp4' : 'webm'}`;
      a.click();
    };

    mediaRecorder.start();
    console.log(' Recording started...');
  }

  export function stopRecording() {
    playStopRecording();
    if (mediaRecorder) {
      mediaRecorder.stop();
    }
    recording = false;
  }

  export function startWebSocketStream() {
    connectWebSocket();

    mediaRecorder = new MediaRecorder(mediaStream, {
      mimeType: 'video/webm'
    });

    mediaRecorder.ondataavailable = (e) => {
      if (e.data.size > 0) {
        e.data.arrayBuffer().then((buffer) => {
          sendChunk(buffer);
        });
      }
    };

    mediaRecorder.onstop = () => {
      sendStop();
    };

    mediaRecorder.start(1000); // gi mi giy
    recording = true;
  }

  export function stopWebSocketStream() {
    if (mediaRecorder && recording) {
      mediaRecorder.stop();
    }
    closeSocket();
    recording = false;
  }

  function toggleMore() {
    showMore = !showMore;
  }

  function closeMore() {
    showMore = false;
  }
</script>

<!-- Viewer full height -->
<div class="relative flex flex-col h-full bg-black overflow-hidden">
  <!-- Video chim ton b -->
  <video
    bind:this={videoElement}
    autoplay
    playsinline
    class="w-full h-full object-contain"
    on:loadedmetadata={() => videoElement.play()}>
  </video>

  <!-- Action bar -->
  <div
    class="fixed bottom-0 left-0 w-full px-6 py-4 bg-black/50 backdrop-blur-sm
         shadow-lg flex flex-wrap items-center justify-center gap-3 border-t z-50">
    <!-- Capture -->
    <button
      on:click={capture}
      class="px-4 py-2 rounded-full bg-green-500 text-white font-medium
           shadow hover:bg-green-600 transition flex items-center gap-2">
       <span>Capture</span>
    </button>

    <!-- Recording -->
    <button
      on:click={startRecording}
      class="px-4 py-2 rounded-full bg-blue-500 text-white font-medium
           shadow hover:bg-blue-600 transition flex items-center gap-2 disabled:opacity-50"
      disabled={recording}>
       <span>Start</span>
    </button>

    <button
      on:click={stopRecording}
      class="px-4 py-2 rounded-full bg-red-500 text-white font-medium
           shadow hover:bg-red-600 transition flex items-center gap-2 disabled:opacity-50"
      disabled={!recording}>
       <span>Stop</span>
    </button>

    <!-- More (dropdown) -->
    <div class="relative">
      <button
        on:click={toggleMore}
        class="w-12 h-12 flex items-center justify-center rounded-full bg-gray-700 text-white
             shadow-md hover:bg-gray-800 transition">
        
      </button>

      {#if showMore}
        <div
          class="absolute bottom-14 left-1/2 -translate-x-1/2 bg-white text-gray-800
               rounded-lg shadow-lg w-48 py-2 z-50">
          <!-- Toggle camera -->
          <button on:click={toggleCamera} class="w-full px-4 py-2 text-left hover:bg-gray-100 flex items-center gap-2">
             Chuyn Camera ({facingMode === 'user' ? 'Trc' : 'Sau'})
          </button>

          <button
            on:click={() => {
              startWebSocketStream();
              closeMore();
            }}
            class="w-full px-4 py-2 text-left hover:bg-gray-100 flex items-center gap-2 disabled:opacity-50"
            disabled={recording}>
             Start Stream
          </button>

          <button
            on:click={() => {
              stopWebSocketStream();
              closeMore();
            }}
            class="w-full px-4 py-2 text-left hover:bg-gray-100 flex items-center gap-2 disabled:opacity-50"
            disabled={!recording}>
             Stop Stream
          </button>
        </div>
      {/if}
    </div>
  </div>

  <!-- Nt back: ch mobile (n trn md tr ln) -->
  <button
    class="absolute top-2 left-2 bg-white text-black rounded-full p-2 shadow-md block md:hidden"
    on:click={onClose}>
    <ArrowLeft size="20" />
  </button>
</div>

<style></style>
