<script>
  import { onMount, onDestroy } from 'svelte';

  import { K2Split, K2Pane } from '@layout/k2';
  import Icon from '@components/Icon.svelte';

  import Faces from './faces/Faces.svelte';
  import CamView from '@components/CamView.svelte';
  import Modal from '@components/ui/modal/Modal.svelte';
  import SettingsPage from '@components/settings/SettingsPage.svelte';
  import { hotkeys } from './stores/hotkeyStore.js';
  import Hotkey from '@components/Hotkey.svelte';
  // import { autoRegister, preloadComponents } from '$lib/componentLoader';

  let selectedFace = 'info';
  //export let keyOption = 'Escape'; // mc nh

  function changeFace(face) {
    selectedFace = face;
  }

  let open = false;
  let modalOption = { width: '80%', height: '80%', title: '' };

  // Lng nghe phm ESC
  function handleKeydown(e) {
    let combo = '';
    if (e.ctrlKey) combo += 'Ctrl+';
    if (e.altKey) combo += 'Alt+';
    if (e.shiftKey) combo += 'Shift+';
    combo += e.key;

    // gi callback nu c ng k
    hotkeys.subscribe((map) => {
      if (map[combo]) {
        map[combo](e);
      }
    });
  }

  onMount(() => {
    window.addEventListener('keydown', handleKeydown);
    //preloadComponents('HomeDetail', 'UserDetail');
    // autoRegister();
  });

  onDestroy(() => {
    window.removeEventListener('keydown', handleKeydown);
  });
</script>

<div class="h-screen flex overflow-hidden">
  <!-- Menu tri -->
  <div class="bg-gray-200 transition-all duration-300" style="width: {selectedFace === 'cam' ? '0px' : '50px'}">
    <div
      class="menu-left h-full w-full bg-blue-100 border-r flex flex-col items-center justify-between py-4 overflow-hidden">
      <!-- Top menu -->
      <div class="flex flex-col border-b gap-y-5">
        <button
          class="p-2 rounded-full hover:bg-green-600 text-white shadow-md transition"
          on:click={() => changeFace('info')}>
          <Icon name="info-circle" />
        </button>

        <button
          class="p-2 rounded-full hover:bg-blue-600 text-white shadow-md transition"
          on:click={() => changeFace('cam')}>
          <Icon name="video" />
        </button>

        <button
          class="p-2 rounded-full hover:bg-red-600 text-white shadow-md transition"
          on:click={() => changeFace('list')}>
          <Icon name="list-ul" />
        </button>

        <button
          class="p-2 rounded-full bg-gray-500 hover:bg-gray-600 text-white shadow-md skeleton"
          on:click={() => changeFace('skeleton')}>
          <Icon name="key-skeleton" />
        </button>

        <div class="divide"></div>
      </div>

      <!-- Bottom menu -->
      <div class="flex flex-col items-center">
        <button
          class="p-2 rounded-full hover:bg-gray-100 text-white shadow-md transition"
          on:click={() => {
            open = true;
            modalOption = { width: '80%', height: '80%', title: 'Thit lp ti khon' };
          }}>
          <Icon name="settings" />
        </button>
      </div>
    </div>
  </div>

  <!-- Ni dung -->
  <div class="flex-1 overflow-auto bg-white">
    {#if selectedFace === 'info'}
      <Faces />
    {:else if selectedFace === 'cam'}
      <CamView onClose={() => (selectedFace = 'info')} />
    {:else}
      <div class="p-4">Coming soon... {selectedFace}</div>
    {/if}
  </div>
</div>

<Modal {...modalOption} bind:open closeOnOutsideClick={false}>
  <SettingsPage />
</Modal>

<!-- ng k hotkeys -->
<Hotkey combo="Escape" on:pressed={() => (selectedFace = 'info')} />
<Hotkey combo="Ctrl+q" on:pressed={() => (selectedFace = 'info')} />
<Hotkey
  combo="Ctrl+s"
  on:pressed={(e) => {
    e.preventDefault();
    console.log('Save!');
  }}>
</Hotkey>
